import React from 'react';
import github from '../assets/icons/github.svg'
import instagram from '../assets/icons/instagram.svg'
import mail from '../assets/icons/mail.svg'
import linkedin from '../assets/icons/linkedin.svg'

export default function Socials(props){
    let classes = "socials"
    if (props.className.length > 1){
        classes += " " + props.className;
    }
    return(
        <div className={classes}>
            <a href="https://github.com/asimonson1125">
                <img alt="Github" src={github} />
            </a>
            <a href="https://www.instagram.com/an_a.simonson/">
                <img alt='Instagram' src={instagram} />
            </a>
            <a href= "https://www.linkedin.com/in/simonsonandrew/">
                <img alt='Linkedin' src={linkedin} />
            </a>
            <a href='mailto: asimonson1125@gmail.com'>
                <img alt="E-mail" src={mail} />
            </a>
        </div>
    )
}